body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p {
    font-size: 1vw;
}

body {
  width: 50%;
  margin: 0 auto;
}

.header {
    padding-top: 30px;
    margin: auto;
}

.icon {
    height: 3vw;
    border-radius: 50%;
    margin-right: .5vw;
}

.me-img {
    height: 100%;
    width: 100%;
}

.title {
    color: black;
    font-size: 40px;
}

.subtitle {
    margin-top: -10px;
    font-size: 1.5vw;
    color: grey;
}

.about-title {
    font-size: 25px;
    color: grey;
}

.grid-height {
    height: 20vw;
}

.NavLink {
    display: block;
    margin-top: 10px;
    font-size: 1.3vw;
}

/* tablet in horizontal mode */
@media only screen and (max-height: 1200px) and (max-width: 1400px) {
    p {
        font-size: 1.2vw;
    }

    .icon {
        height: 3vw;
        margin-right: 1vw;
    }

    .title {
        font-size: 3vw;
    }

    .subtitle {
        font-size: 1.5vw;
    }

    .about-title {
        font-size: 3vw;
    }

    .grid-height {
        height: 22vw;
    }

    .NavLink {
        display: block;
        margin-top: 10px;
        font-size: 1.3vw;
    }
}

/* tablet in portrait mode */
@media only screen and (min-height: 800px) and (max-width: 1400px) {
    body {
        width: 75%;
    }

    .title {
        font-size: 3vw;
    }

    .subtitle {
        font-size: 2vw;
    }

    .about-title {
        font-size: 4vw;
    }

    p {
        font-size: 1.5vw;
    }

    .icon {
        height: 3vw;
        margin-right: 1vw;
    }

    .grid-height {
        height: 35vw;
    }
}

/* phone in portrait mode */
@media only screen and (max-width: 600px){
    .icon {
        height: 9vw;
        margin-right: 3vw;
        margin-bottom: 3vw;
    }

    .header{
        width: 100%;
    }

    p {
        font-size: 4vw;
    }

    .subtitle {
        font-size: 5vw;
    }

    .title {
        font-size: 9vw;
    }

    .about-title {
        font-size: 6vw;
    }

    .grid-height {
        height: 160vw;
    }

    .NavLink {
        display: block;
        margin-top: 10px;
        font-size: 4.1vw;
    }
}

/* phone in landscape mode */
@media only screen and (min-width:500px) and (max-height: 600px) {
    .title {
        font-size: 4vw;
    }

    .about-title {
        margin-top: -10px;
    }

    .grid-height {
        height: 10vw;
    }
}

/* foldable phones */
@media only screen and (max-width:00px) {
    .grid-height {
        height: 200vw;
    }
}

